/**
 * Master CSS for loading all stylesheets
 *
 * Uses stylus as a CSS preoprocessor
 * @link http://learnboost.github.io/stylus/
 *
 * Use BEM methodology for class names
 * > block, element, modifier
 * @link http://bem.info/
 *
 * Use SMACSS for folder organization
 * > layout, module, object and state
 * @link https://smacss.com/
 *
 * Use concetric-css standard for property order
 * > placement, visibility, layers, content, text
 * @link http://rhodesmill.org/brandon/2011/concentric-css/
 */
/* Common fonts */
@import '../font/Hero/Hero.css';
/* Vendor lib */
@import '../../node_modules/normalize.css/normalize.css';
/* Common tools */
body.dev:before {
  position: fixed;
  right: 5px;
  bottom: 5px;
  background: #ffd700;
  padding: 0.5em;
  z-index: 9999;
  font-weight: bold;
  font-size: 1.1rem;
  color: #000;
  content: 'mobile';
  pointer-events: none;
}
@media only screen and (max-width: 25em) {
  body.dev:before {
    content: 'small';
  }
}
@media only screen and (min-width: 62.5em) {
  body.dev:before {
    content: 'medium';
  }
}
@media only screen and (min-width: 68.75em) {
  body.dev:before {
    content: 'large';
  }
}
@media only screen and (min-width: 106.25em) {
  body.dev:before {
    content: 'xl';
  }
}
.is-loading {
  position: relative;
}
.is-loading:after {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 10;
  margin: -10px 0 0 -10px;
  border: 5px solid rgba(0,0,0,0.1);
  border-top: 5px solid rgba(0,0,0,0.5);
  border-radius: 100%;
  height: 20px;
  width: 20px;
  animation: rotation 0.6s infinite linear;
  content: '';
}
.is-disabled {
  opacity: 0.3;
  pointer-events: none;
}
.is-hidden {
  display: none !important;
}
.is-invisible {
  visibility: hidden !important;
}
.is-visible {
  visibility: visible !important;
}
@-moz-keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@-webkit-keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@-o-keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.u-table {
  display: table;
}
.u-table-cell {
  display: table-cell;
  vertical-align: middle;
}
.u-relative {
  position: relative;
  width: 100%;
  height: 100%;
}
.u-flex {
  display: flex;
}
.u-flex--column {
  flex-direction: column;
}
.u-flex--row {
  flex-direction: row;
}
.u-flex--full {
  flex: 1;
}
.u-overflow--scroll {
  overflow: auto;
}
.u-ftw {
  position: absolute;
  bottom: 10px;
  left: 90px;
  transform: rotate(-90deg);
  transform-origin: 0 100%;
  padding: 0 10px;
  text-align: center;
  font-family: 'Bangers', cursive;
  font-size: 8rem;
  color: $dark-green;
  will-change: transform;
}
@media only screen and (max-width: 25em) {
  .u-ftw {
    display: none;
  }
}
/* Common style */
* {
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
}
*:focus {
  outline: none;
}
html,
body {
  height: 100%;
}
html {
  font-size: 62.5%;
}
body {
  overflow: hidden;
  margin: 0;
  background: #eee;
  font-family: 'Hero', sans-serif;
  font-size: 16px;
  color: #000;
}
main {
  position: relative;
  width: 100%;
  height: 100%;
}
a {
  padding: 0px 2px;
  background-color: #ececec;
  text-decoration: none;
  color: inherit;
  font-weight: 700;
}
svg {
  width: 100%;
  height: 100%;
}
/* Layout */
.layout,
.layout > article {
  width: 100%;
  height: 100%;
}
.l-main {
  overflow: auto;
}
.l-main > div {
  width: 100%;
  height: 100%;
}
.l-resume {
  padding: 20px;
  text-align: center;
}
.l-resume__wrapper {
  display: inline-block;
  padding: 20px;
  box-shadow: 2px 2px 20px -10px rgba(0,0,0,0.5);
  background: #fff;
  width: 100%;
  max-width: 1000px;
  text-align: left;
}
@media only screen and (min-width: 62.5em) {
  .l-resume__wrapper {
    padding: 20px 40px;
    width: auto;
    min-width: 1000px;
  }
}
.l-resume__title {
  margin: 20px 0;
  text-align: right;
}
.l-resume__title h1 {
  margin: 0;
  line-height: 35px;
  font-size: 4rem;
  font-weight: 700;
}
.l-resume__title h2 {
  margin: 0;
  text-transform: uppercase;
  font-weight: 200;
  font-size: 2.36rem;
}
.l-resume__category {
  padding: 20px 0;
  font-size: 0;
}
.l-resume__category h3 {
  margin: 0 0 10px 0;
  text-align: left;
  font-weight: 700;
  font-size: 1.5rem;
}
@media only screen and (min-width: 62.5em) {
  .l-resume__category h3 {
    text-align: right;
  }
}
.l-resume__category-list,
.l-resume__detail-list {
  display: block;
  vertical-align: top;
  padding: 0;
  margin: 0;
  list-style: none;
}
.l-resume__category-list li,
.l-resume__detail-list li {
  position: relative;
  font-size: 1.6rem;
}
@media only screen and (min-width: 62.5em) {
  .l-resume__category-list,
  .l-resume__detail-list {
    display: inline-block;
  }
}
.l-resume__category-list {
  padding-right: 8px;
  width: 25%;
  text-align: right;
}
.l-resume__category-list > li {
  display: none;
  font-weight: 300;
  color: #575757;
}
@media only screen and (min-width: 62.5em) {
  .l-resume__category-list > li {
    display: block;
  }
}
.l-resume__detail-list {
  border-left: 2px solid #ddd;
  padding-left: 8px;
  width: 100%;
}
@media only screen and (min-width: 62.5em) {
  .l-resume__detail-list {
    width: 75%;
  }
}
.l-resume__detail-list > h3 {
  display: none;
}
@media only screen and (min-width: 62.5em) {
  .l-resume__detail-list > h3 {
    display: block;
  }
}
.l-resume__detail-list h3 {
  color: #fff;
}
.l-resume__detail-list li {
  margin: 15px 0;
}
.l-resume__detail-list li:last-child {
  margin-bottom: 0;
}
.l-resume__detail-list li:first-child {
  margin-top: 0;
}
.l-resume__detail-list li h3 {
  display: none;
}
@media only screen and (min-width: 62.5em) {
  .l-resume__detail-list li h3 {
    display: block;
  }
}
.l-resume__detail-list li p {
  margin: 3px 0;
}
.l-resume__detail-list--personal li {
  margin: 0;
}
.l-resume__date {
  margin: 10px 0 5px 0;
  font-weight: 700;
  font-size: 1.6rem;
}
.l-resume__date:before {
  content: "";
  width: 8px;
  height: 8px;
  background: #ddd;
  position: absolute;
  margin-left: -13px;
  margin-top: 5px;
  border-radius: 100%;
}
.l-resume__float-detail {
  display: none;
  position: absolute;
  left: -150px;
  text-align: right;
  width: 130px;
  font-weight: 300;
  color: #575757;
}
@media only screen and (min-width: 62.5em) {
  .l-resume__float-detail {
    display: block;
  }
}
/* Module */
.m-sidebar {
  z-index: 20;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: absolute;
  top: 0;
  left: -100px;
  background: #626469 url("../media/design/right-shadow.png") repeat-y 100% 0;
  width: 100px;
  height: 100%;
  transition-duration: 0.25s;
}
.m-sidebar.m-sidebar--private-active:before {
  bottom: auto;
  top: 30px;
}
.m-sidebar.m-sidebar--links-active:before {
  bottom: 245px;
}
.m-sidebar.m-sidebar--search-active:before {
  bottom: 145px;
}
.m-sidebar.m-sidebar--settings-active:before {
  bottom: 45px;
}
@media only screen and (min-width: 62.5em) {
  .m-sidebar {
    left: 0;
  }
}
.m-sidebar:before {
  content: "";
  position: absolute;
  right: 0;
  border-style: solid;
  border-width: 10px 14px 10px 0;
  border-color: transparent #fff transparent transparent;
  width: 0;
  height: 0;
  transition-duration: 0.25s;
}
.m-sidebar__item {
  opacity: 0.3;
  width: 100px;
  height: 100px;
}
.m-sidebar__item p {
  margin: 10px 0;
  text-align: center;
  font-size: 1.6rem;
  color: #fff;
}
.m-sidebar__item.is-active {
  opacity: 1;
}
.m-sidebar__item--top {
  position: absolute;
  top: 0;
}
.m-sidebar__item--top p {
  font-size: 1.4rem;
}
.m-sidebar__svg-wrapper {
  display: block;
  width: 100px;
  height: 50px;
}
.m-sidebar__svg-wrapper svg {
  fill: #fff;
}
.m-modal {
  z-index: 30;
}
modal,
.m-modal,
.m-modal__overlay,
.m-modal__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.m-modal__overlay {
  background: rgba(0,0,0,0.3);
}
.m-modal__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.m-modal__container {
  position: relative;
  border-radius: 3px;
  box-shadow: 4px 4px 10px -4px rgba(0,0,0,0.5);
  background: #fff;
  width: 450px;
  min-height: 200px;
}
.m-modal__container .o-button--close {
  position: absolute;
  top: 0;
  right: 0;
}
.m-modal__container .o-button--close svg {
  fill: #5d5d5d;
}
.m-modal__title {
  margin: 0;
  padding: 25px 0;
  border-bottom: 1px solid #ddd;
  text-align: center;
  font-weight: 400;
  font-size: 2.5rem;
  color: #5d5d5d;
}
.m-modal__content {
  padding: 20px 30px;
}
.m-modal__content p {
  text-align: center;
  line-height: 30px;
  font-size: 2rem;
  color: #5d5d5d;
}
.m-modal__form {
  text-align: center;
  padding: 30px 50px;
}
.m-modal__form .m-login__input input {
  text-align: center;
}
.m-modal__vote {
  padding: 40px 0;
  clear: both;
  overflow: hidden;
  text-align: center;
}
.m-modal__vote .m-search-result__icon-vote {
  margin: 0 5px;
  width: 50px;
  height: 50px;
  float: right;
}
.m-modal__vote-wrapper {
  display: inline-block;
}
.m-modal__vote-wrapper > .m-search-result__icon-vote:hover > svg,
.m-modal__vote-wrapper > .m-search-result__icon-vote:hover ~ .m-search-result__icon-vote > svg {
  fill: #ffd100;
}
.m-modal__footer {
  padding: 0px 0 20px 0;
  text-align: center;
}
.m-modal__button {
  padding: 10px;
  margin: 0 10px;
  min-width: 90px;
  width: auto;
  height: auto;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  color: #5d5d5d;
}
/* Object */
a,
button,
.o-button,
.o-button--custom,
.o-button--like,
.o-button--close {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  cursor: pointer;
}
.o-button,
.o-button--custom,
.o-button--close {
  display: inline-block;
}
.o-button:active,
.o-button--custom:active,
.o-button--close:active {
  opacity: 0.5;
  transform: translateY(1px);
}
.o-button--like:active svg {
  opacity: 0.5;
  transform: translateY(1px);
}
.o-button {
  display: inline-block;
  border-radius: 4px;
  border: 1px solid #ddd;
  background: #fff;
  width: 40px;
  height: 40px;
}
.o-button--close {
  position: relative;
  width: 50px;
  height: 50px;
  cursor: pointer;
}
